import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
// eslint-disable-next-line import/prefer-default-export
export const store = new Vuex.Store({
    state: {
        message: '',
        isLoggedIn: false,
    },
    mutations: {
        setMessage: (state, message) => {
            state.message = message;
        },
    },
});
